
















import { Component, Vue, Prop } from 'vue-property-decorator';
import LandingPage from '@/components/LandingPage.vue';
import NavBar from '@/components/NavBar.vue';
import { NavItem } from '@/models/navItem';
import SlideIn from '@/components/Animations/SlideIn.vue';

@Component({
  components: {
    LandingPage,
    NavBar,
    SlideIn
  }
})
export default class HomePage extends Vue {
  navItems: NavItem[] = [
    { displayName: 'Home', sectionName: 'section-home', active: true },
    { displayName: 'About', sectionName: 'section-about', active: false },
    { displayName: 'Portfolio', sectionName: 'section-portfolio', active: false },
    { displayName: 'Contact', sectionName: 'section-contact', active: false }
  ];

  selectNavItem(navItem: NavItem) {
    this.navItems = this.navItems.map(item =>
      item === navItem ? { ...item, active: true } : { ...item, active: false }
    );
  }
}
