





















import { Component, Vue, Prop } from 'vue-property-decorator';
import SlideInFromTop from '@/components/Animations/SlideInFromTop.vue';
import { NavItem } from '@/models/navItem';
import { scrollToSection } from '@/helper/scrollToSection';
import NavBarItem from './NavBarItem.vue';
import Menu from 'mdi-vue/Menu.vue';

@Component({
  components: {
    SlideInFromTop,
    NavBarItem,
    Menu
  }
})
export default class NavBar extends Vue {
  fixed = false;
  mobile = true;
  mobileNav = false;
  @Prop() navItems!: NavItem[];

  mounted() {
    this.mobile = screen.width < 720;
    this.listenForScrolling();
  }

  toggleNavItem(navItem: NavItem) {
    this.$emit('selected', navItem);
    this.toggleMobileNav();
    scrollToSection(navItem.sectionName);
  }

  toggleMobileNav() {
    this.mobileNav = !this.mobileNav;
  }

  listenForScrolling() {
    document.addEventListener('resize', () => {
      this.mobile = screen.width < 720;
    });

    document.addEventListener('scroll', () => {
      try {
        if (
          document.documentElement.scrollTop >=
          document.documentElement.clientHeight
        ) {
          this.fixed = true;
        } else {
          this.fixed = false;
        }
        this.mobileNav = false;
      } catch (error) {
        //
      }
    });
  }
}
